.home__header{
    display: flex;
    justify-content: space-around;
    padding-top: 40px;
    padding-bottom: 80px;
}



.header__logo{
    font-weight: 500;
    font-size: 30px;
    line-height: 1.16;
}

.header__nav-link{
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
    text-transform: uppercase;
    
}
.header__nav-link:not(:last-child){
    margin-right: 39px;
}
